body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  background-color: #fff;
  padding: 10px;
}

.info-item {
  margin: 0;
}

